@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@800&display=swap);
@-webkit-keyframes home{from{opacity:0;transform:translate3d(0, -25px, 0)}to{opacity:1;transform:translate3d(0, 0, 0)}}@keyframes home{from{opacity:0;transform:translate3d(0, -25px, 0)}to{opacity:1;transform:translate3d(0, 0, 0)}}@-webkit-keyframes navdot{from{opacity:0}to{opacity:1}}@keyframes navdot{from{opacity:0}to{opacity:1}}body{margin:0;font-family:'Raleway', sans-serif;-moz-osx-font-smoothing:grayscale;background-color:#363437}#name{font-family:Georgia;font-size:2.1em;color:whitesmoke;margin-bottom:1em;margin-top:2em;font-weight:normal}.projects-container{-webkit-animation:home 1.5s ease-in-out forwards;animation:home 1.5s ease-in-out forwards;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;max-width:70em;margin:auto;padding-bottom:5em}.credentials-container{-webkit-animation:home 1.5s ease-in-out forwards;animation:home 1.5s ease-in-out forwards;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;max-width:70em;margin:auto;padding-bottom:5em}#canvas{z-index:-1}#me{-webkit-animation:home 1.5s ease-in-out forwards;animation:home 1.5s ease-in-out forwards;margin-top:3.75em;height:12em;width:12em}.icon{height:3.5em;width:auto;transition:all .2s ease-in-out}.icon:hover{transform:scale(1.075)}#icon-container{-webkit-animation:home 2s ease-in-out forwards;animation:home 2s ease-in-out forwards;margin-top:6.5em}nav{-webkit-animation:home 0.75s ease-in-out forwards;animation:home 0.75s ease-in-out forwards;display:flex;justify-content:space-between;margin:0 auto;max-width:30em;opacity:0;padding:3em}nav a{color:white;font-weight:700;margin:0 2em;letter-spacing:0.08em;position:relative;text-decoration:none;transition:all .2s ease-in-out}nav a:hover{transform:scale(1.05)}nav a:before{border-radius:50%;content:'';background-color:#ff7f7d;left:0;height:10px;opacity:0;position:absolute;top:50%;transform:translate(-200%, -50%);width:10px}nav a.active:before{-webkit-animation:navdot 0.25s ease-in-out forwards 0.25s;animation:navdot 0.25s ease-in-out forwards 0.25s}.app{padding:2em;text-align:center}@media screen and (max-width: 768px){body{font-size:14px}h1{font-size:3.5em}nav{padding:1em}nav a{margin:0 1em}nav a:before{bottom:150%;top:auto;left:50%;transform:translate(-50%, 0)}.app{padding:3em 2em 2em}.content--inner{padding:1em}}

.App {
  text-align: center;
  overflow-x: hidden;
}

.card-container{border-radius:8px;box-shadow:0 4px 4px 4px #232323;background-color:#d8d8d8;margin:3em 3em;width:25em;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column}.card-container:hover{box-shadow:0 4px 8px 8px #232323}.card-container:hover .content{height:59%}.card-container .header{border-radius:8px 8px 0 0;padding:0.2em;-webkit-backface-visibility:hidden;backface-visibility:hidden}.card-container .header .title{font-size:1.3em;opacity:90%;letter-spacing:0.08em;border-bottom:1px solid rgba(0,0,0,0.2);margin-left:1.4em;margin-right:1.4em;margin-bottom:0;padding-bottom:0.3em;color:#060606}.card-container .header .title-dots{color:#ff7f7d}.card-container .content{text-align:left;padding:0 2.1em 1.5em 2.1em;align-content:center}.card-container .content h4{color:#101010;font-size:1.1em;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-top:1em;margin-bottom:0;position:relative;z-index:1;display:flex}.card-container .content h4:before{content:'';text-align:center;width:100%;height:3px;background:#f9454a;opacity:0.5;position:absolute;bottom:0;z-index:-1;padding:0 1px;transition:cubic-bezier(0.68, -0.55, 0.27, 1.55) 320ms}.card-container .content h4:hover:before{height:5px}.card-container .content h5{font-weight:600;font-size:0.8em;font-family:Montserrat;color:#454545;margin-top:0.5em;margin-bottom:0.5em}.card-container .content p{font-weight:500;font-size:0.8em;font-family:Montserrat;color:#454545;margin-top:0}@media screen and (max-width: 768px){.card-container{font-size:12px}.card-container .header .title{margin-left:1.2em;margin-right:1.2em}.card-container .content{padding:0 1.9em 1.5em 1.9em}}

.post *{margin:0;padding:0}.post{border-radius:8px;box-shadow:0 4px 4px 4px #232323;margin:3em 5em;width:17em;height:23em;display:flex;overflow:hidden;flex-direction:column;position:relative;color:#101010}.post:hover{box-shadow:0 4px 8px 8px #232323}.post:hover .body_post{height:59%}.post .header_post{width:100%;height:30%;background:#ddd;position:absolute;top:0}.post .header_post img{max-width:100%;height:auto}.post .body_post{width:100%;height:58%;background:#e7e7e7;position:absolute;bottom:0;display:flex;justify-content:center;align-items:center;transition:cubic-bezier(0.68, -0.55, 0.27, 1.55) 320ms}.post .body_post .post_content{width:80%;height:80%;background:#e7e7e7;position:relative}.post .body_post .post_content .title-dots{color:#ff7f7d;font-size:.8em}.post .body_post .post_content h1{font-size:20px;font-weight:bold;opacity:90%;margin-bottom:0.4em}.post .body_post .post_content p{font-size:14px;font-weight:normal;font-family:Montserrat}.post .body_post .post_content .container_infos{width:100%;height:auto;display:flex;justify-content:center;position:absolute;bottom:0;border-top:1px solid rgba(0,0,0,0.2);padding-top:10px}.post .body_post .post_content .container_infos span{font-size:12px;text-transform:uppercase;opacity:0.5;letter-spacing:1px;font-weight:bold;margin:0.25em}.post .body_post .post_content .container_tags{display:flex;flex-direction:column}.post .body_post .post_content .container_tags span{font-size:12px;text-transform:uppercase;opacity:0.5;letter-spacing:1px;font-weight:bold}.post .body_post .post_content .container_tags .tags ul{display:flex;flex-wrap:wrap;justify-content:center}.post .body_post .post_content .container_tags .tags ul li{font-size:0.8em;list-style:none;margin-left:8px;margin-top:4px;position:relative;z-index:1;display:flex;justify-content:center}.post .body_post .post_content .container_tags .tags ul li:first-child{margin-left:0}.post .body_post .post_content .container_tags .tags ul li:before{content:'';text-align:center;width:100%;height:3px;background:#f9454a;opacity:0.5;position:absolute;bottom:0;z-index:-1;padding:0 1px;transition:cubic-bezier(0.68, -0.55, 0.27, 1.55) 320ms}.post .body_post .post_content .container_tags .tags ul li:hover:before{height:5px}@media screen and (max-width: 768px){.post{font-size:17px}}

